import axios from 'axios';

// lower level RPC services, should only be used by fetcher.js

let requestCount = 0;

export const getLastPersistedBlockNumber = (baseURL) => {
    return axios.post(baseURL, {
        jsonrpc: '2.0',
        method: "reporting_getLastPersistedBlockNumber",
        params: [],
        id: requestCount++,
    })
};

export const getAddresses = (baseURL) => {
    return axios.post(baseURL, {
        jsonrpc: '2.0',
        method: "reporting_getAddresses",
        params: [],
        id: requestCount++,
    })
};

export const addAddress = (baseURL, address) => {
    return axios.post(baseURL, {
        jsonrpc: '2.0',
        method: "reporting_addAddress",
        params: [address],
        id: requestCount++,
    })
};

